.tabs {
  &__nav {
    @include flex-all-center;
    margin-bottom: 42px;
  }

  &__nav-item:not(:last-child) {
    margin-right: 107px;
  }

  &__nav-btn {
    font-weight: 400;
    font-size: 28px;
    line-height: 143%;
    text-align: center;
    color: rgba(255, 255, 255, 0.5);
    transition: color .3s ease-in-out;

    &--active {
      color: $light-color;
    }
  }

  &__panel {
    display: none;

    &--active {
      display: block;
    }

    p {
      margin-top: 0;
      margin-bottom: 0.5em;
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.01em;
      color: $light-color;
    }
  }


}

