.prodact-card {
  padding-bottom: $section-offset;
  background-color: $bg-dark-color;

  &__title {
    margin-bottom: 40px;
  }

  &__info {
    grid-column: 7 / 6 span;
  }
  &__top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: $section-offset;
  }

  &__price {
    display: inline-block;
    padding: 24px 80px 16px;
    border-radius: 100px 100px 0 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 92%;
    color: $light-color;
    background-color: #404040;
  }

  &__btn {
    display: inline-block;
    padding: 30px 111px;
    border-radius: 0 150px 150px 300px;
    font-weight: 800;
    font-size: 24px;
    line-height: 92%;
    color: $light-color;
  }
}
.prodact-card-images {
  grid-column: 5 span;
  overflow: hidden;
}
.prodact-card-images-slider {
  min-height: 465px;
}
.card-img {
  --br-img-card: 10px;
  @include flex-all-center;
  // padding: 20px;
  border-radius: var(--br-img-card);
  width: 100%;
  height: 100%;
  cursor: pointer;

  img {
    border-radius: var(--br-img-card);
    max-width: 100%;
    object-fit: cover;
  }
}

.prodact-card-images-nav {
  overflow: hidden;
  max-height: 96px;

  .swiper-slide {
    border-radius: 5px;
    min-width: 90px;
    opacity: 0.6;
    transition: opacity 0.3s ease-in-out;
    cursor: pointer;

    .swiper-slide-thumb-active {
      opacity: 1;
    }
  }

  .swiper-slide-thumb-active {
    opacity: 1;
  }
}

.options {
  @include flex-h-center;
  flex-direction: column;
  gap: 1.5em;


  &__item {
    display: grid;
    grid-template-columns: 0.8fr 2fr;
    column-gap: 10px;
    span {
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.01em;
      color: $light-color;
    }
  }
}


